@import "../../global.scss";




.works {
  background-color: rgb(243, 243, 250);
  display: flex;
  align-items: center;
  justify-content: center;
  position: relative;

  .arrow {
    height: 50px;
    position: absolute;
    cursor: pointer;

    @include mobile {


      height: 20px;

      // display: none;
    }

    &.left {
      left: 100px;
      transform: rotateY(180deg);

      @include mobile{
        left:10px;
      }
    }

    &.right {
      right: 100px;
      @include mobile{
        right:10px;
      }
    }
  }

  .slider {
    height: 350px;
    display: flex;
    position: absolute;
    left: 0;
    transition: all 1s ease-out;

    @include mobile {
      height: 100vh;
      // flex-direction: column;
      // justify-content: center;
    }

  

    .container {
      width: 100vw;
      display: flex;
      align-items: center;
      justify-content: center;

      .item {
        width: 700px;
        height: 100%;
        background-color: white;
        border-radius: 20px;
        display: flex;
        align-items: center;
        justify-content: center;

        @include mobile {
          width: 80%;
          height: 150px;
          margin: 15px 0;
        }

        .left {
          flex: 4;
          height: 80%;
          display: flex;
          align-items: center;
          justify-content: center;

          .leftContainer {
            width: 90%;
            height: 70%;
            display: flex;
            flex-direction: column;
            justify-content: space-between;

      

            .imgContainer {
              width: 40px;
              height: 40px;
              border-radius: 50%;
              background-color: rgb(245, 179, 155);
              display: flex;
              align-items: center;
              justify-content: center;

              @include mobile {
                width: 20px;
                height: 20px;
              }

              img {
                width: 25px;

                @include mobile {
                  width: 15px;
                }
              }
            }
            h2 {
              font-size: 20px;

              @include mobile{
                font-size: 11px;
              }
            }
            p {
              font-size: 13px;
              @include mobile{
                font-size: 7px;
              }
            }
            span {
              font-size: 12px;
              font-weight: 600;
              text-decoration: underline;
              cursor: pointer;
            }
          }
        }
        .right {
          flex: 8;
          height: 100%;
          display: flex;
          align-items: center;
          justify-content: center;
          overflow: hidden;

          img{
            width: 400px;
            height: 300px;
            @include mobile{
              width: 300px;
              height: 200px;
            }
          }
          .rotate{
            
            transform: rotate(-20deg);
            @include mobile{
              width: 300px;
              height: 200px;
            }
            
          }
          .rotate1{
            
            transform: rotate(-40deg);
            @include mobile{
              width: 300px;
              height: 200px;
            }
          }
        }
      }
    }
  }
}
.works .slider .container .item .left .leftContainer {
  font-size: 15px;
  overflow: hidden;
}
